export default {
	props: {
		tipText: {
			type: String,
			default: '选择位置'
		},
		descText: {
			type: String,
			default: '使用当前定位或在地图上标记位置'
		},
	},
	data() {
		return {
			center: {lng: 109.45744048529967, lat: 36.49771311230842},
			zoom: 13,
			pointList:[
				{lng:'108.49926175379778',
				 lat:'36.60449676862417',
				 name:'张三',
				 uid:'1'
				},
				{lng:'107.59926175379778',
				 lat:'36.40449676862417',
				 name:'李四',
				 uid:'2'
				},
				{lng:'109.79926175379778',
				 lat:'36.50449676862417',
				 name:'王五',
				 uid:'3'
				}
			]
		};
	},
	mounted() {
		/* this.getLocation()
		this.initMapH() */
	},
	onLoad() {
		//this.ontrueGetList();
	},
	methods:{
		handler ({BMap, map}) {
		  for (var p of this.pointList) {
			  var point = new BMap.Point(p.lng, p.lat);
			  var icon = new BMap.Icon("../../static/img/map/mark.png",new BMap.Size(32,32));
			  
			  var marker = new BMap.Marker(point,{icon:icon}) // 创建标注
			  map.addOverlay(marker) // 将标注添加到地图中
			  var label = new BMap.Label(p.name, {offset: new BMap.Size(5, 35)});
			  label.setStyle({
				  padding: "2px",
				  fontSize: "12px",
				  height: "20px",
				  backgroundColor: "#fbffd7",
				  color: "#333333",
				  fontWeight: "bold",
				  fontFamily: "微软雅黑",
				  border: "1px solid #999999",
				  maxWidth: "none",
				  position: "relative",
			  });
			  marker.setLabel(label);
			  }
		  //var point = new BMap.Point(109.49926175379778, 36.60449676862417)
		  map.centerAndZoom(point, 10)
		  
		},
		getClickInfo (e) {
			
		}
	},
}

